import React from "react";
import "./index.css";

const Popup = ({ showWrap, showList, animate, children }) => {
  return (
    showWrap && (
      <div className={`role-wrap ${showList ? "show" : ""}`}>
        <div
          className={`opacity-wrap ${showList ? "show" : ""}`}
          onClick={() => animate(false)}
        ></div>
        {children}
      </div>
    )
  );
};

export default Popup;